<script>
// import { Cell } from 'minu-ui'
// export default {
//   components: {
//     Cell: Cell
//   }
// }
import { Swipe, SwipeItem, Range } from 'mint-ui'

export default {
  data() {
    return {
      rangeValue: 100
    }
  },
  components: {
    // [Cell.name]: Cell
    [Swipe.name]: Swipe,
    [SwipeItem.name]: SwipeItem,
    [Range.name]: Range
  }
}
</script>
<style lang="scss">
  /* span {
    font-size: 100px;
  } */
  // .name {
  //   width: 375px;
  //   height: 500px;
  //   background: #ccc;
  // }
  .mySwipe {
    height: 320px;
  }
  img {
    width: 375px;
    height: 400px;
    display: block;
  }
</style>



